/* Основной контейнер */
.main-menu {
  background-color: #1e1e1e; /* Очень темный серый фон */
  color: #ffffff; /* Белый текст */
  padding: 20px;
  min-height: 100vh; /* Высота экрана */
  font-family: 'Arial', sans-serif;
}

/* Заголовок */
.main-menu-title {
  font-size: 2.5rem;
  color: #ff6f00; /* Ярко-оранжевый акцент */
  margin-bottom: 20px;
  text-align: center;
}

/* Контейнер карточек */
.menu-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Адаптивная сетка */
  gap: 20px;
  justify-content: center;
  padding: 10px 0;
}

/* Карточки */
.menu-card {
  background-color: #2a2a2a; /* Темно-серый фон карточек */
  border-radius: 10px;
  text-align: center;
  padding: 20px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5); /* Тени */
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.menu-card:hover {
  background-color: #ff6f00; /* Оранжевый фон при наведении */
  transform: scale(1.05); /* Увеличение карточки */
}

/* Изображения */
.card-image {
  width: 150%; /* Увеличенное изображение */
  height: auto; /* Сохраняем пропорции */
  border-radius: 10px;
  margin-bottom: 15px;
}

/* Кнопки */
.card-button {
  padding: 6px 10px; /* Уменьшенные размеры кнопки */
  background-color: rgba(255, 111, 0, 0.7); /* Полупрозрачный оранжевый фон */
  color: #ffffff; /* Белый текст */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.card-button:hover {
  background-color: rgba(255, 111, 0, 1); /* Яркий оранжевый при наведении */
  transform: scale(1.1); /* Легкое увеличение кнопки */
}

/* Две строки для мобильных устройств */
@media (max-width: 600px) {
  .menu-container {
    grid-template-columns: repeat(2, 1fr); /* Две колонки */
    gap: 15px;
  }

  .card-button {
    padding: 5px 8px; /* Уменьшение размера кнопок на мобильных устройствах */
  }
}
